import styled from "styled-components";

export const Container = styled.div`
  height: 100vh;
  width: 100%;
`;

export const Header = styled.header`
  width: 100%;
  height: 64px;
  background-color: #ddddff;
`;

export const Body = styled.div`
  width: 100%;
  height: calc(100vh - 64px);
`;